:root {
  --color-white-main: #fcfcfc;
  --color-yellow-main: #f8d24e;
  --color-yellow-secondary: #efd478;
  --color-green-main: #84a178;
  --color-green-secondary: #aec6a5;
  --color-brown-main: #d6936d;
  --color-grey-main: #ababab;
  --color-grey-secondary: #7a7a7a;
  --color-black-main: #131313;
  --color-green-button: #0a8817;
  --default-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --lt-sp-main: .04em;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

link {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 12px;
  font-family: var(--default-font);
  letter-spacing: .02em;
  color: var(--color-white-main);
}

@media screen and (min-width: 1200px) {
  body {
    font-size: 16px;
  }
}

.container {
  min-width: 320px;
  max-width: 448px;
  background-color: var(--color-green-main);
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 720px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button.buy__button, .button.location__button, .button.read-more__button {
  background-color: var(--color-yellow-main);
}

.button.buy__button:hover, .button.location__button:hover, .button.read-more__button:hover {
  background-color: var(--color-green-button);
}

.button.buy__button:focus, .button.location__button:focus, .button.read-more__button:focus {
  color: var(--color-white-main);
  background-color: var(--color-green-button);
}

.button.buy__button:disabled, .button.location__button:disabled, .button.read-more__button:disabled {
  background-color: var(--color-grey-main);
}

.button.franchise__button, .button.products__button {
  border: 2px solid var(--color-yellow-main);
  color: var(--color-black-main);
  background-color: rgba(0, 0, 0, 0);
}

.button.franchise__button:hover, .button.products__button:hover {
  border-color: var(--color-green-button);
}

.button.franchise__button:focus, .button.products__button:focus {
  color: var(--color-white-main);
  border-color: var(--color-green-button);
}

.button.franchise__button:disabled, .button.products__button:disabled {
  border-color: var(--color-grey-main);
}

.button {
  font-family: var(--default-font);
  color: var(--color-black-main);
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 12px 34px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.28571;
}

.button.products__button {
  width: 62px;
  height: 24px;
  border-radius: 8px;
  padding: 0;
  font-size: 8px;
}

@media screen and (min-width: 1200px) {
  .button.products__button {
    width: auto;
    height: auto;
    border-radius: 10px;
    padding: 12px 34px;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.28571;
  }
}

.button.location__button {
  width: 196px;
}

.button.location__button .arrow-one {
  margin-left: 10px;
}

.button.franchise__button {
  width: 196px;
}

.button.franchise__button .arrow-one {
  margin-left: 10px;
}

.link.about-info-link {
  color: var(--color-yellow-main);
  font-size: 8px;
  font-weight: 600;
  line-height: 1.21875;
}

@media screen and (min-width: 1200px) {
  .link.about-info-link {
    font-size: 14px;
    line-height: 1.21429;
  }
}

.link.about-info-link:hover {
  color: var(--color-green-button);
}

.link.about-info-link:focus {
  color: var(--color-white-button);
}

.link.about-info-link:disabled {
  color: var(--color-grey-main);
}

.arrow-one {
  stroke: currentColor;
  fill: none;
  margin-left: 2px;
  position: relative;
  top: 1px;
}

.products .container {
  background-image: url("products-background-1200.9eabd8ee.jpg");
}

.about, .advantages {
  background-color: #fff;
}

.customer-reviews .container {
  background-image: url("customers-background-1200.bfd05605.jpg");
}

